import React from "react";
import "./Button.css";

const Button = props =>
  <a
    className="Button"
    href={props.href}
    style={{
      display:
        props.href !== undefined && props.href.length < 1
          ? "none"
          : "inline-block"
    }}
  >
    {props.children}
  </a>;

export default Button;



